:root {
  --selected-color: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo-img {
  vertical-align: bottom;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list > li:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  flex-direction: column;
  justify-content: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--selected-color);
}

.is-active:after {
  content: "";
  background-color: var(--selected-color);
  border-radius: 3px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
